.products {
  padding-top: 80px;
  padding-bottom: 40px;

  background-color: #fff;
  
  @media screen and (min-width: 768px) {
    padding-top: 40px;
    padding-bottom: 60px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.products-container {
  margin-right: auto;
  margin-left: auto;
  @media screen and (max-width: 479px) {
    padding-left: 20px;
    padding-right: 20px;
    width: 100%;
  }
  @media screen and (min-width: 480px) and (max-width: 767px) {
    padding-left: 20px;
    padding-right: 20px;
    width: 480px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding-left: 30px;
    padding-right: 30px;
    width: 768px;
  }
  @media screen and (min-width: 1280px) {
    width: 1280px;
  }
}

.products-title-top {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  color: var(--section-color-top);

  @media screen and (min-width: 320px) and (max-width: 767px) {
    font-size: 18px;
    line-height: 1.16;
    margin-bottom: 15px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 26px;
    line-height: 1.15;
    margin-bottom: 18px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
    margin-bottom: 16px;
  }
}

.products-title-head {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--accent-color);

  @media screen and (min-width: 320px) and (max-width: 767px) {
    font-size: 34px;
    line-height: 1.14;
    margin-bottom: 150px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 48px;
    line-height: 1.14;
    margin-bottom: 150px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.13;
    margin-bottom: 250px;
  }
}

.products-list {
  align-content: center;
  display: flex;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    display: block;
  }
  @media screen and (min-width: 768px) {
    margin-right: -16px;
  }
  @media screen and (min-width: 1280px) {
    margin-right: -32px;
  }
}

.products__item {
  position: absolute;
  display: block;
   border-radius: 24px;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-transition: transform 400ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: transform 400ms ease;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    height: 489px;
    width: 280px;
    margin: 0 auto 160px auto;
    padding: 160px 40px 40px 40px;
    &:last-child {
        margin-bottom: 0;
    }
    }


  @media screen and (min-width: 768px) {
    height: 473px;
    width: 224px;
    padding: 118px 32px 40px 32px;
    flex-basis: calc((100% - 3 * 16px) / 3);
    &:not(:last-child) {
      margin-right: 16px;
      flex-wrap: wrap;
    }
  }

  @media screen and (min-width: 1280px) {
    height: 535px;
    width: 395px;
    padding: 165px 77px 60px 77px;
    flex-basis: calc((100% - 3 * 32px) / 3);
    &:not(:last-child) {
      margin-right: 32px;
      flex-wrap: wrap;
    }
  }
}

.products__item-ice {
  display: block;
  background: var(--section-color-top);
  position: relative;
  
}
.products__item-ice::before {
  content: '';
  display: block;
  background-position: top;
  position: absolute;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  
  padding-bottom: 100%;
  left: 50%;
  transform: translate(-50%);
  @media screen and (min-width: 320px) and (max-width: 767px) {
    top: -150px;
    background-image: url('../images/products/mobile/drink-mobile-1x.png');
  
    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image: url('../images/products/mobile/drink-mobile-2x.png');
    }
  }
  @media screen and (min-width: 768px) {
    top: -150px;
    background-image: url('../images/products/tablet/pic-tablet-1x.png');
    
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
        background-image: url('../images/products/tablet/pic-tablet-2x.png');
    }
  }

  @media screen and (min-width: 1280px) {
    top: -250px;
    background-image: url('../images/products/desktop/pic1-desktop-1x.png');
    
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
        background-image: url('../images/products/desktop/pic1-desktop-2x.png');
    }
  }
}

