  @media screen and (min-width: 768px) {
    top: -150px;
    background-image: url('../images/products/tablet/pic-tablet-1x-1.png');

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
        background-image: url('../images/products/tablet/pic-tablet-2x-1.png');
