.products-item-title {
 
  font-family: 'Titan One';
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--light-color);
  margin-bottom: 32px;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    font-size: 20px;
    line-height: 1.15;
  }
  @media screen and (min-width: 768px) {
    font-size: 20px;
    line-height: 1.15;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1;
  }
}

.products-item-text {
  transform-style:flat;
  font-family: 'DM Sans';
  text-align: center;
  letter-spacing: 0.04em;
  color: var(--light-color);
  font-weight: 700;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 32px;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    font-size: 14px;
    line-height: 1.52;
    width: 200px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 14px;
    line-height: 1.76;
    width: 160px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.76;
    width: 241px;
    margin-bottom: 60px;
  }
}

.products-dots-item {
 
  content: '';
  fill: #ffffff;
  
  display: flex;
  justify-content: center;
  list-style: none;
}
.products-dots-list {
  display: block;
  margin-bottom: 32px;
}
.products-btn-more {
  cursor: pointer;
  position: absolute;
  left: 50%;
  bottom: 60px;
  transform: translateX(-50%);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  fill: var(--accent-color);
  background-color: var(--light-color);
  border: 0.5px;
  
 
  margin: 0 auto;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: var(--hover-btn-dark);
    fill: var(--light-color);
  }
}

.face {
  display: block;
   width: 100%;
  
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.back {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow:hidden;
  z-index:-1;
  display: block;
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
  @include mobile {
    .products-item-text{
      
      margin-top: 150px;
    }
  }
  @include tablet {
    .products-item-text{
      margin-top: 100px;
    }
  }
  @include desktop {
    .products-item-text{
      margin-top: 180px;
    }
  }
 
  // &:before{
  //   content: "";
	//   // position: absolute;
  //   // top: 0;
  //   // left: 0;
	//   width: 100%;
	//   height: 100%;
	//   z-index: -1;
  //   background-size:100% 100%;
	//   background: inherit
  //   }
}
.front, .back{
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.front{
  z-index: 1;
  // height: 100%;
}
.flipped {
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}

