    display: none;
  }
}

@media screen and (min-width: 768px) {
  .mob-menu__open {
    margin-right: 32px;
  }
}

.mob-menu__open span {
  width: 100%;
  height: 1px;
  background-color: var(--accent-color);
  position: absolute;
  top: 5.5px;
  left: 0;
}

.mob-menu__open:before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--accent-color);
  position: absolute;
  top: 0;
  left: 0;
}

.mob-menu__open:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--accent-color);
  position: absolute;
  bottom: 0;
  left: 0;
}

.mob-menu__close {
  height: 20px;
  width: 20px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 20px 17px 26px auto;
  display: block;
  position: relative;
}

.mob-menu__close:hover span:before, .mob-menu__close:focus span:before, .mob-menu__close:hover span:after, .mob-menu__close:focus span:after {
  background-color: var(--accent-color);
}

.mob-menu__close span:before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--light-color);
  transition: background-color var(--main-transition);
  position: absolute;
  top: 10px;
  left: 0;
  transform: rotate(45deg);
}

.mob-menu__close span:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--light-color);
  transition: background-color var(--main-transition);
  position: absolute;
  top: 10px;
  left: 0;
  transform: rotate(-45deg);
}

